html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  max-width: 1010px;
  margin: 0 auto;
}

body {
  font-family: 'Inter', sans-serif;
  font-size: 12px;
  line-height: 22px;
  font-weight: 400;
  color: #151515;
}
